.dashboard {
  background-color: #000000;

  .dashboard-header {
    margin-top: 24px;

    h1 {
      color: #ffffff;
      font-size: 20px;
      font-weight: 900;
      text-align: center;
    }
  }

  .dashboard-section {
    margin-top: 48px;
    display: flex;
    justify-content: space-around;

    .dashboard-sidenav {
      position: relative;

      .sidenav {
        width: 200px;

        &.list-group {
          margin-right: 0;
        }

        .sidenav-item {
          display: flex;
          background-color: #111111;
          // padding: 15px 12px;

          .sidenav-link {
            font-size: 12px;
            // font-weight: 300;
            margin-left: 20px;
            color: #9c9c9c;
            padding-top: 8px;
            letter-spacing: 0.2px;

            .sidenav-icon {
              width: 32px;
              height: 32px;
            }

            &.btn-connect-wifi {
              width: 68px;
              height: 24px;
              margin-top: 8px;
              padding: 7px 17px;
              border-radius: 16px;
              background-color: #322f89;
              font-weight: 500;
              letter-spacing: 0.45px;
              line-height: 1;
              text-align: center;
              color: #ffffff;
            }
          }
        }
      }

      .wifi-tooltip {
        & > .tooltip {
          margin-left: 20px;
        }

        & .wifi-tooltip-inner {
          width: 180px;
          background-color: #444444;
          padding-top: 12px;
          padding-bottom: 12px;

          & .wifi-list {
            width: 100%;
            max-height: 140px;
            overflow-y: scroll;
            object-fit: contain;
            background-color: inherit;

            .list-group-item {
              height: 22px;
              padding: 4px 6px;
              text-align: left;
              line-height: 1;
              font-size: 12px;
              line-height: 1;
              letter-spacing: 0.2px;
              border: 0;
              border-radius: 5px;
              color: #ffffff;
              background-color: transparent;
              margin-bottom: 2px;
              cursor: pointer;
            }

            .wifi-item {
              .wifi-icon {
                margin-right: 6px;
              }

              span {
                vertical-align: text-bottom;
              }

              &:hover {
                background-color: #838383;
                text-decoration: underline;
              }
            }
          }
        }

        & .arrow {
          &::before {
            border-width: 0.4rem 0.6rem 0.4rem 0;
            border-right-color: #444444;
          }
        }
      }
    }

    .dashboard-contents {
      padding-top: 300px;
      display: flex;
      justify-content: center;

      .col {
        width: 239px;
      }
      .card {
        justify-content: center;
        border-radius: 5px;
        background-image: radial-gradient(
          circle at 18% -2%,
          #333333,
          #222222 33%,
          #1a1a1a 76%
        );

        .card-item {
          width: 100%;
          height: 239px;

          .card-link {
            margin-top: 74px;
            border: 0;
            font-size: 20px;
            width: 100%;
            min-height: 48px;
            text-align: center;
            border-radius: 24px;
            background-color: #171717;
            color: #ffffff;
          }

          &:hover {
            border: 1px solid #322f89;

            .card-link {
              background-color: #322f89;
            }
          }
        }

        &:hover {
          background-image: radial-gradient(
            circle at 50% 0%,
            #444444,
            #333333 50%,
            #222222 89%
          );

          .card-link {
            background-color: #222222;
          }
        }
      }
    }

    .dashboard-offbar {
      // width: 200px;

      .auth {
        line-height: 3;
        display: flex;
        /* flex-direction: revert; */
        justify-content: space-between;
        padding: 0;
        width: 169px;
        height: 50px;
        border-radius: 30px;
        background-color: #222222;
        color: #ffffff;

        .auth-icon {
          position: relative;
          width: 50px;
          height: 50px;
          border-radius: 28px;
          background-color: #444444;
          cursor: pointer;

          img {
            position: absolute;
            transform: translate(50%, 50%);
          }
        }

        .auth-signout {
          position: relative;
          width: 32px;
          height: 32px;
          border-radius: 28px;
          background-color: #444444;
          margin: 9px;
          cursor: pointer;

          img {
            position: absolute;
            transform: translate(22%, 22%);
          }
        }
      }

      .auth-tooltip {
        & > .tooltip {
          margin-top: 6px;
        }

        & .auth-tooltip-inner {
          padding: 10px 8px;
          background-color: #444444;

          .auth-menu {
            width: 80px;
            font-size: 12px;

            .auth-menu-item {
              width: 100%;
              height: 28px;
              line-height: 1;
              background: inherit;
              padding: 8px;
              border-radius: 5px;
              cursor: pointer;
              border: 0;
              margin-bottom: 2px;

              &:hover {
                background-color: #838383;
                text-decoration: underline;
              }
            }
          }
        }

        & .arrow {
          &::before {
            border-width: 0 0.5rem 0.5rem;
            border-bottom-color: #444444;
          }
        }
      }

      .offbar {
        margin-top: 530px;
        width: 200px;

        .offbar-item {
          margin-bottom: 20px;
          border-radius: 30px;
          background-color: #222222;
          color: #ffffff;
          cursor: pointer;

          .offbar-icon {
            width: 24px;
            height: 24px;
          }

          .offbar-link {
            color: #ffffff;
            font-size: 16px;
            margin-left: 12px;
          }

          &:hover {
            // border: solid 1px #322f89;
            background-color: #322f89;
          }
        }
      }
    }
  }
}

/*
 * Modal
 */
.modal {
  .modal-dialog.wifi-modal {
    .modal-content {
      padding: 24px 12px 12px 12px;
      box-sizing: border-box;
      border-radius: 5px;
      background-color: #333333;
      color: #ffffff;

      & > [class|='modal'] {
        background-color: #222222;
      }

      .modal-header {
        border: 0;
        border-radius: 5px 5px 0 0;

        img {
          width: 24px;
          margin: 0 10px 8px 0;
        }
      }

      .modal-body {
        padding: 0 1rem;
        font-size: 12px;

        .form-group {
          margin-bottom: 0.5rem;
          margin-top: 12px;

          &.checkbox-input {
            display: flex;

            label {
              font-size: 10px;
            }
          }

          label {
            user-select: none;
          }

          input[type='password'] {
            width: 100%;
            height: 28px;
            padding: 3px 10px;
            color: #ffffff;
            background-color: #111111;
            border: solid 0.5px #777777;
            border-radius: 2px;
            outline: none;
            box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.5);
          }

          input[type='checkbox'] {
            position: static;
            margin: 0 6px 0 0;
            height: 16px;
          }
        }
      }

      .modal-footer {
        border-radius: 0 0 5px 5px;
        border: 0;
        padding-top: 0.3rem;

        .btn {
          width: 47px;
          height: 16px;
          font-size: 10px;
          padding: 0;
          line-height: 1.2;
          border-radius: 2px;
          box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
          background-color: #555555;
        }
      }
    }
  }
}
